.orderBg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-image: url(../images/wool-742770_1920.jpg);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    filter: grayscale(90%) saturate(10%) brightness(0.7);
    opacity: .2;
    z-index: -1;
}


.ring button {
    z-index: 5;
    position: fixed;
    top: 62%;
    left: 50%;
    transform: translateX(-50%);
    width: 23vh;
    height: 23vh;
    border-radius: 30%;
    transform-origin: left -100px;
    animation: animate 10s linear infinite, startCircle 2s linear 1 forwards;
    font-size: 28px;
    color: white;
    background-color: rgba(34, 45, 66, 0.493);
    border: 2px solid white;
    box-shadow: 0 0 10px 2px white;
    cursor: pointer;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
}

.ring button:hover {
    box-shadow: 0 0 10px 8px white;
}

.activeOrder button {
    animation: animate 10s linear infinite, startCircle 2s linear 1 forwards, changePosition 1s linear 1 forwards;
}

@keyframes changePosition {
    0% {
        top: 65%;
        left: 50%;
        width: 23vh;
        height: 23vh;
        transform-origin: left -100px;
    }

    100% {
        top: 58%;
        left: 15%;
        width: 16vh;
        height: 16vh;
        transform-origin: left -60px;
    }
}



@keyframes startCircle {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}


.chooseCategory {
    position: fixed;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
    color: white;
    text-transform: uppercase;
    font-size: 40px;
    padding: 100px;

}

.chooseCategory2 {
    animation: chooseCategoryMove 1s linear 1 forwards;
}

@keyframes chooseCategoryMove {
    0% {
        position: fixed;
        top: 30%;
        left: 50%;
        transform: translateX(-50%);
        color: white;
        text-transform: uppercase;
        font-size: 40px;
        padding: 100px;
    }

    100% {
        position: fixed;
        top: 34%;
        left: 15%;
        transform: translateX(-50%);
        font-size: 25px;
        color: white;
        text-transform: uppercase;
        padding: 100px;
    }
}


.ring .pluszaki {
    animation-delay: -2s, 0s, 0s;
    background-image: url(../images/Products/pluszak.png);
}

.ring .pufy {
    animation-delay: -4s, 0s, 0s;
    background-image: url(../images/Products/pufa.png);
}

.ring .koce {
    animation-delay: -6s, 0s, 0s;
    background-image: url(../images/Products/koc.png);
}


.ring .poduszki {
    animation-delay: -8s, 0s, 0s;
    background-image: url(../images/Products/poduszka.png);
}

.ring .kosze {
    animation-delay: -10s, 0s, 0s;
    background-image: url(../images/Products/kosz.png);
}

.ring button p {
    background-color: rgba(0, 0, 0, 0.74);
    font-size: 25px;
    text-transform: uppercase;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    padding: 5px 0;
}

.activeOrder button p {
    font-size: 20px;
}


@keyframes animate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.titleProducts {
    position: absolute;
    top: 30vh;
    left: 52%;
    color: #222;
    font-size: 40px;
    z-index: 3;
    animation: startCircle 1s linear 1 forwards;
}

.orderList {
    position: relative;
    top: 0vh;
    left: 30vw;
    width: 70vw;
    min-height: 200vh;
    padding: 20vh 0;
    background-color: rgba(255, 255, 255, 0.226);
    animation: startCircle 1s linear 1 forwards;
}

.orderList span {
    position: absolute;
    top: 12vh;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 15vh;
    background-color: rgba(0, 0, 0, 0.637);
    color: white;
    font-size: 30px;
    padding: 3vh 5vw;
}

.orderList ul {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 40vh;
    left: 50%;
    width: 80%;
    min-height: 100vh;
    transform: translateX(-50%);
    justify-content: space-around;
    flex-wrap: wrap;
    flex-grow: 1;
}

.orderList li {
    width: 300px;
    height: 350px;
    background-color: rgba(114, 146, 118, 0.363);
    color: white;
    font-size: 20px;
    margin: 50px;
    text-decoration: none;
    list-style: none;
    box-shadow: 0 0 15px 5px white;
    position: relative;
    transition: .5s;
    border-radius: 10px;
}


.orderList li:hover {
    transform: scale(1.1);
}

.link {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.orderList li p {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 20%;
    color: white;
    font-size: 50px;
    background-color: rgba(18, 29, 15, 0.65);
    border-radius: 0 0 10px 10px;
}

.orderList li img {
    height: 85%;
}

.easyOrder {
    color: white;
    font-size: 30px;
    position: relative;
    height: 100vh;
    width: 100vw;
    font-size: 32px;
    color: blanchedalmond;
}

.easyOrder .easyOrderha {
    position: absolute;
    top: 85vh;
    left: 2vw;
    transform: rotate(0deg);
}

.easyOrderhb {
    position: absolute;
    top: 85vh;
    right: 3vw;
    transform: rotate(0deg);
}

@media (orientation:portrait) and (min-width: 320px) {

    .ring button {
        z-index: 0;
        position: absolute;
        top: 55%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 17vh;
        height: 17vh;
        transform-origin: left -40px;
    }

    .activeOrder button {
        display: none;
    }

    .chooseCategory {
        position: absolute;
        top: 5%;
        left: 50%;
        transform: translateX(-50%);
        font-size: 30px;
        padding: 0px;
    }

    .chooseCategory2 {
        display: none;
    }

    .ring button p {
        font-size: 18px;
    }

    .activeOrder button p {
        font-size: 18px;

    }


    .titleProducts {
        position: absolute;
        top: 40vh;
        left: 50%;
        transform: translateX(-50%);
        width: 95vw;
        color: #222;
        font-size: 28px;
        z-index: 3;
        margin: 5vh auto;
    }

    .orderList {
        position: relative;
        top: 0vh;
        left: 0vw;
        width: 100vw;
        min-height: 400vh;
        padding: 20vh 0;
        background-color: rgba(255, 255, 255, 0.55);
    }

    .orderList span {
        position: absolute;
        top: 12vh;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        height: auto;
        background-color: rgba(0, 0, 0, 0.637);
        color: white;
        font-size: 24px;
        padding: 3vh 5vw;
    }

    .orderList ul {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 50vh;
        left: 50%;
        transform: translateX(-50%);
        width: 100vw;
        min-height: 100vh;
        justify-content: space-around;
        flex-wrap: wrap;
    }

    .orderList li {
        width: 70vw;
        height: 50vh;
        margin: 50px auto;
        box-shadow: 0 0 15px 5px rgb(73, 95, 72);
    }


    .orderList li:hover {
        transform: none;
    }


    .orderList li p {
        height: 20%;
        font-size: 36px;
        line-height: 150%;
    }


    .easyOrder {
        font-size: 20px;
    }

    .easyOrder .easyOrderha {
        width: 100vw;
        top: 80vh;
        left: 50%;
        transform: translateX(-50%);
    }

    .easyOrderhb {
        width: 100vw;
        top: 88vh;
        left: 50%;
        transform: translateX(-50%);
    }
}

