        padding: 0px;
    }

    .chooseCategory2 {
        display: none;
    }

    .ring button p {
        font-size: 14px;
    }

    .activeOrder button p {
        font-size: 14px;

    }


    .titleProducts {
        position: absolute;
        top: 50vh;
